<template>
  <div id="mine">
    <!-- 顶部红色区域 -->
    <div class="mine_top" v-if="userInfo">
      <div class="mine_t_i">
        <div class="mine_t_i_l">
          <van-image
            width="1.6rem"
            height="1.6rem"
            round
            fit="cover"
            :src="userInfo.avatar"
          />
        </div>
        <div class="mine_t_i_c">
          <h3>{{ userInfo.nickname }}</h3>
          <p>
            ID：<span>{{ userInfo.uid }}</span
            ><van-icon name="edit" />
          </p>
        </div>
        <div class="mine_t_i_r"><van-icon name="setting-o" @click="set"/></div>
      </div>
    </div>
    <!-- 内边距0.2rem区域 -->
    <div class="mine_main">
      <!-- 优惠券 -->
      <div class="cash">
        <ul class="cash_ul" v-if="userInfo">
          <li>
            <p>我的余额</p>
            <p>{{ userInfo.now_money }}</p>
          </li>
          <li>
            <p>佣金</p>
            <p>{{ userInfo.brokerage_price }}</p>
          </li>
          <li>
            <p>优惠券</p>
            <p>{{ userInfo.couponCount }}</p>
          </li>
        </ul>
      </div>
      <!-- 订单 -->
      <div class="po">
        <MineNav title="我的订单" :flag="true"></MineNav>
        <ul class="poUl">
          <li v-for="(i, index) in poList" :key="index">
            <List :i="i" :index="index" :nums="orderNum" v-if="orderNum"></List>
          </li>
        </ul>
      </div>
      <!-- 我的服务 -->
      <!-- <div class="mine_saas">
        <MineNav title="我的服务" :flag="false"></MineNav>
        <ul class="saas_ul">
          <li v-for="(i, index) in sassList" :key="index * 100">
            <List :i="i" :ids="index"></List>
          </li>
        </ul>
      </div> -->
      <!-- 底部图片 -->
      <!-- <img
        src="http://47.115.51.185/h5/img/support.37442214.png"
        alt=""
        class="bt_img"
      /> -->
    </div>
  </div>
</template>
<script>
import axios from "axios";
import MineNav from "../components/mine/mineNav";
import List from "../components/mine/mineList";
// 引入vuex的快捷函数
// import {mapState} from 'vuex'
export default {
  components: {
    MineNav,
    List,
  },
  data() {
    return {
      userInfo: null,
      poList: [
        { name: "paid", text: "待付款" },
        { name: "completed", text: "待发货" },
        { name: "sign", text: "待收获" },
        { name: "comment-o", text: "待评价" },
        { name: "after-sale", text: "待售后/退款" },
      ],
      sassList: [
        { name: "manager-o", text: "会员中心" },
        { name: "gift-o", text: "砍价记录" },
        { name: "friends-o", text: "我的推广" },
        { name: "balance-o", text: "我的余额" },
        { name: "location-o", text: "地址信息" },
        { name: "star-o", text: "我的收藏" },
        { name: "balance-pay", text: "优惠券" },
        { name: "service-o", text: "联系客服" },
      ],
      // 订单徽标显示数量
      orderNum:null,
    };
  },
  computed:{

  },
  created() {
    let token = this.$store.state.token;
    axios({
      method: "GET",
      url: "http://47.115.51.185/api/user",
      headers: {
        //设置http请求的头信息
        //在头部里面通过Authori-zation 这个字段传递token
        "Authori-zation": "Bearer " + token,
        // 'token':token
      },
      data: {
        token: token,
      },
    }).then((res) => {
      this.userInfo = res.data.data;
    });
    // 我的服务
    // /api/userinfo
    // /api/user 订单状态
    // /api/menu/user
    this.axios.get("/api/user").then(res=>{
      this.orderNum=res.data.data.orderStatusNum;
      // console.log(this.orderNum);
      // console.log(res);
    })
  },
  methods:{
    set(){
      this.$router.push({path:'/mine/set'});
    }
  }
};
</script>
<style lang="less" scoped>
#mine {
  width: 100vw;
  background-color: #f5f5f5;
  .mine_top {
    width: 100vw;
    box-sizing: border-box;
    background-color: #e93323;
    border-radius: 0 0 0.3rem 0.3rem;
    .mine_t_i {
      width: 100vw;
      box-sizing: border-box;
      padding: 0.7rem 0.4rem 1rem;
      display: flex;
      // 左
      .mine_t_i_l {
        width: 1.6rem;
        height: 1.6rem;
        box-sizing: border-box;
        border-radius: 50%;
        border: 0.03rem solid #f5f5f5;
        position: relative;
        .van-image__img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
      // 中
      .mine_t_i_c {
        flex: 1;
        text-align: left;
        box-sizing: border-box;
        padding-left: 0.35rem;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        h3 {
          width: 100%;
          font-size: 0.5rem;
          color: #fff;
        }
        p {
          width: 100%;
          margin-top: 0.12rem;
          display: flex;
          align-content: center;
          font-size: 0.323rem;
          color: hsla(0, 0%, 100%, 0.6);
          i {
            font-size: 0.37rem;
            margin-left: 0.12rem;
          }
        }
      }
      // 右
      .mine_t_i_r {
        i {
          font-size: 0.48rem;
          color: #fff;
        }
      }
    }
  }
  .mine_main {
    padding: 0 0.2rem;
    position: relative;
    top: -0.5rem;
    //优惠券
    .cash {
      width: 100%;
      background-color: #fff;
      height: 1.8rem;
      border-radius: 0.06rem;
      display: flex;
      align-items: center;
      .cash_ul {
        width: 100%;
        display: flex;
        justify-content: space-around;
        text-align: center;
        li {
          flex: 1;
          p:nth-child(1) {
            color: #aaa;
            font-size: 0.38rem;
          }
          p:nth-child(2) {
            color: #282828;
            font-size: 0.45rem;
            margin-top: 0.1rem;
          }
        }
        li:not(:nth-child(3)) {
          border-right: 1px solid #eeeeee;
        }
      }
    }
    // 我的订单
    .po {
      margin-top: 0.2rem;
      border-radius: 0.1rem;
      background-color: #fff;
      // 订单
      .poUl {
        display: flex;
        padding: 0.4rem 0px;
        // flex-wrap: wrap;
         text-align: center;
        li {
          font-size: 0.26rem;
          flex: 1;
          // width: 30vw;
          .mine_list {
            .mine_text {
              color: #454545;
              font-weight: 700;
            }
            .mine_icon {
              margin-bottom: 0.18rem;
              // 文字
            }
          }
        }
      }
    }
    // 我的服务
    .mine_saas {
      margin-top: 0.2rem;
      border-radius: 0.1rem;
      background-color: #fff;
      .saas_ul {
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 0.4rem;
        li {
          width: 25%;
          margin-top: 0.3rem;
          text-align: center;
        }
      }
    }
    .bt_img {
      width: 2.19rem;
      height: 0.74rem;
      margin: 0.54rem auto;
      display: block;
    }
  }
}
</style>