<template>
  <div class="mine_list" @click="tz(index, ids)">
    <div class="mine_icon">
      <van-icon :name="i.name" :badge="num" />
    </div>
    <div class="mine_text">
      {{ i.text }}
    </div>
  </div>
</template>
<script>
export default {
  // index是订单传的
  // ids是我的服务传的
  props: ["i", "index", "nums", "ids"],
  data() {
    return {
      num: "",
    };
  },
  methods: {
    tz(index, ids) {
      if (index < 4) {
        this.$router.push("/order/list/" + index);
      } else if (index == 4) {
        this.$router.push("/order/refund_list");
      }
      switch (ids) {
        case 0:
          break;
        case 1:
          break;
        case 2:
          break;
        case 3:
          break;
        case 4:
          this.$router.push("/address");
          break;
        case 5:
          this.$router.push("/collection");
          break;
        case 6:
          this.$router.push("/coupons");
          break;
        case 7:
          break;
      }
    },
  },
  created() {
    switch (this.index) {
      case 0:
        this.num = this.nums.unpaid_count;
        break;
      case 1:
        this.num = this.nums.unshipped_count;
        break;
      case 2:
        this.num = this.nums.received_count;
        break;
      case 3:
        this.num = this.nums.evaluated_count;
        break;
      case 4:
        this.num = this.nums.refund_count;
        break;
    }
    if (this.num == 0) {
      this.num = "";
    }
  },
};
</script>
<style lang="less">
.poUl {
  li {
    .mine_list {
      .mine_icon {
        .van-icon {
          color: #ff4d3d;
          font-size: 0.7rem;
        }
      }
      .mine_text {
        color: #454545;
        font-size: 0.35rem;
      }
    }
  }
}
.mine_saas {
  .saas_ul {
    li {
      .mine_list {
        .mine_icon {
          .van-icon {
            color: #ff4d3d;
            font-size: 0.7rem;
          }
        }
        .mine_text {
          color: #333;
          font-size: 0.35rem;
        }
      }
    }
  }
}
</style>